import React, { useEffect, useState } from 'react';
import ApiService from '../services/ApiService';
import DataTable from 'react-data-table-component';

function SDashboard() {
  const [centrelist, setCentrelist] = useState([]);
  const [chrCentrecode, setSelectedCentre] = useState("");
  const [studentlist, setStudentlist] = useState([]);
  const [filteredStudentList, setFilteredStudentList] = useState([]);
  const [columns, setColumns] = useState([]);
  const [search, setSearch] = useState("");
  const [reset, setReset] = useState("");

  useEffect(() => {
    const fetchDataCentrelist = async () => {
      try {
        const response = await ApiService.getCentreList();
        const data = response.data.centrelist;
        setCentrelist(Array.isArray(data) ? data : []);
      } catch (error) {
        console.error('Error fetching centre data:', error);
      }
    };

    fetchDataCentrelist();
  }, []);

  useEffect(() => {
    const resetExam = async () => {
      if (reset) {
        try {
          const response = await ApiService.resetExamOfStudent(reset);
          console.log(response);
        } catch (error) {
          console.error('Error resetting exam:', error);
        }
      }
    };

    resetExam();
  }, [reset]);

  useEffect(() => {
    if (!chrCentrecode) return;

    const fetchStudentlistData = async () => {
      try {
        const response = await ApiService.getListOfStudent(chrCentrecode);
        const students = response.data.Details;
        setStudentlist(students);

        if (students.length > 0) {
          const keys = Object.keys(students[0]);
          const columns = keys.map(key => ({
            name: key.replace(/([A-Z])/g, ' $1').trim(),
            selector: row => row[key],
            sortable: true,
          }));

          columns.push({
            name: 'Actions',
            cell: row => (
              row.ExamStatus === 'Submitted' ? (
                <button
                  className="btn btn-warning"
                  onClick={() => handleRestButtonClick(row)}
                >
                  Reset
                </button>
              ) : null
            ),
            ignoreRowClick: true,
            allowOverflow: true,
            button: true,
          });

          setColumns(columns);
        }
      } catch (error) {
        console.error('Error fetching student data:', error.response ? error.response.data : error.message);
      }
    };

    fetchStudentlistData();
  }, [chrCentrecode]);

  useEffect(() => {
    if (search) {
      const filteredData = studentlist.filter(student =>
        Object.values(student).some(value =>
          value.toString().toLowerCase().includes(search.toLowerCase())
        )
      );
      setFilteredStudentList(filteredData);
    } else {
      setFilteredStudentList(studentlist);
    }
  }, [search, studentlist]);

  const handleCentreChange = (e) => {
    setSelectedCentre(e.target.value);
  };

  const handleSearchChange = (e) => {
    setSearch(e.target.value);
  };

  const handleRestButtonClick = (row) => {
    setReset(row.RollNo);
    alert(`Student Exam Reset: ${row.Name}`);
  };

  return (
    <div className='container mt-0'>
      <div className='row mb-3'>
        <div className='col'>
          <select
            className="form-select"
            value={chrCentrecode}
            onChange={handleCentreChange}>
            <option value="" disabled>Choose centre...</option>
            {centrelist.map((centre, index) => (
              <option key={index} value={centre.centreCode}>
                {centre["ShortName "] ? centre["ShortName "] : 'Unnamed Centre'}
              </option>
            ))}
          </select>
        </div>
        <div className='col'>
          <input
            type='text'
            placeholder="Search..."
            className="form-control"
            value={search}
            onChange={handleSearchChange}
          />
        </div>
      </div>

      <DataTable
        data={filteredStudentList}
        columns={columns}
        pagination
        noDataComponent="No students found for the selected centre."
      />
    </div>
  );
}

export default SDashboard;
