import React from 'react';
import { Col, Row, Nav, Offcanvas, ProgressBar, Card } from 'react-bootstrap';
import { useMediaQuery } from 'react-responsive';
import '../assets/css/question.css';

const NumberOfQuestion = ({ attemptedQuestions, totalQuestions, currentQuestionIndex, handleQuestionClick }) => {
  const [showOffcanvas, setShowOffcanvas] = React.useState(false);
  const isMobile = useMediaQuery({ query: '(max-width: 1200px)' });

  const getQuestionStatus = (number) => {
    if (attemptedQuestions.includes(number)) {
      return 'attempted';
    } else if (currentQuestionIndex === number - 1) {
      return 'notAttempted';
    } else {
      return 'skipped';
    }
  };

  const attemptedCount = attemptedQuestions.length;
  const skippedCount = [...Array(totalQuestions).keys()].filter((number) => getQuestionStatus(number + 1) === 'skipped').length;
  const progressPercentage = (attemptedCount / totalQuestions) * 100;

  const renderQuestionNumber = (number) => {
    const questionStatus = getQuestionStatus(number);
    let colorClass = '';
    switch (questionStatus) {
      case 'attempted':
        colorClass = 'custom-bg-color';
        break;
      case 'skipped':
        colorClass = 'custom-bg-buttom';
        break;
      case 'notAttempted':
        colorClass = 'bg-danger text-black';
        break;
      default:
        colorClass = '';
    }
    return (
      <Nav.Link
        className={`question-number ${colorClass}`}
        onClick={() => handleQuestionClick(number)}>
        {number}
      </Nav.Link>
    );
  };

  return (
    <Col xs={4}>
      {isMobile ? (
        <Offcanvas show={showOffcanvas} onHide={() => setShowOffcanvas(false)} placement="start">
          <Offcanvas.Header closeButton>
            <Offcanvas.Title>Questions</Offcanvas.Title>
          </Offcanvas.Header>
          <Offcanvas.Body>
            <Nav className="flex-column">
              {[...Array(totalQuestions).keys()].map((number) => (
                <Nav.Item key={number + 1}>
                  {renderQuestionNumber(number + 1)}
                </Nav.Item>
              ))}
            </Nav>
          </Offcanvas.Body>
        </Offcanvas>
      ) : (
        <div className="desktop-card">
          <Row>
            <Col style={{ marginLeft: '10px' }}>
              <span className="square-box attemptedCount">{attemptedCount}</span>
            </Col>
            <Col>
              <span className="square-box skippedCount">{skippedCount}</span>
            </Col>
          </Row>
          <Card.Body className="align-items-center cardBodyBorder">
            <ProgressBar animated now={progressPercentage} label={`${Math.round(progressPercentage)}%`} /><br />
            <Nav className="columnbuttom">
              {[...Array(totalQuestions).keys()].map((number) => (
                <Row key={number}>
                  {[...Array(5).keys()].map((i) => {
                    const questionNumber = number * 5 + i + 1;
                    return questionNumber <= totalQuestions ? (
                      <Col style={{ padding: 0 }} key={questionNumber}>
                        {renderQuestionNumber(questionNumber)}
                      </Col>
                    ) : null;
                  })}
                </Row>
              ))}
            </Nav>
          </Card.Body>
        </div>
      )}
    </Col>
  );
};

export default NumberOfQuestion;
