import React, { useContext, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Container, Table, Button } from 'react-bootstrap'; // Use Button from react-bootstrap
import 'bootstrap/dist/css/bootstrap.min.css';
import '../assets/css/login.css';
import RajLogo from '../assets/img/Raj-Logo.png';
import Logo1 from '../assets/img/MBTB-Marathi.png';
import LearnigImg from '../assets/img/Exam-Login.png';
import { UserContext } from '../contexts/UserContext'; // Import UserContext
import ApiService from '../services/ApiService';

const NewResult = () => {
  const [result, setResult] = useState(null);
  const { user } = useContext(UserContext);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  const handleLogout = () => {
    localStorage.clear();
    navigate('/');
  };

  useEffect(() => {
    const fetchResult = async () => {
      try {
        setLoading(true);
        if (user?.username && user?.courseid) {
          const response = await ApiService.getResult(user.username, user.courseid);
          console.log(response);
          setResult(response.data);
        }
        setLoading(false);
      } catch (error) {
        console.error('Error fetching result:', error);
        setLoading(false);
      }
    };

    fetchResult();
  }, [user]);

  if (loading) {
    return <p>Loading...</p>;
  }

  if (!result) {
    return <p>No result data available.</p>;  }

  return (
    <div className='result'>
      <Container className="d-flex align-items-center min-vh-100 p-1 mt-0">
        <div className="row w-100">
          <div className="col-12 col-md-6 col-lg-5">
            <div className="card shadow-sm" style={{ marginLeft: '10px' }}>
              <div className="card-body" style={{ padding: '30px' }}>
              <div className="text-center">
                  <img className="imgcentre mb-4" src={Logo1} style={{width:'150px'}} alt="Online Exam"/>
                </div>
                <h6 className='text-center'>
                  Name: {`${user?.firstname || ''} ${user?.middlename || ''} ${user?.lastname || ''}`.trim()}
                </h6>
                <h6 className='text-center'>
                  Course: {`${user?.courseid || ''}`}
                </h6>
                <Table bordered className="mt-4">
                  <tbody>
                    <tr>
                      <td>Total Questions:</td>
                      <td>{result.TotalQuestions}</td>
                    </tr>
                    <tr>
                      <td>Skipped Questions:</td>
                      <td>{result.Skipped}</td>
                    </tr>
                    <tr>
                      <td>Attempted Questions:</td>
                      <td>{result.Attempted}</td>
                    </tr>
                    
                    <tr>
                      <td>Correct Questions:</td>
                      <td>{result.CorrectQuestions}</td>
                    </tr>
                    <tr>
                      <td>Total Marks:</td>
                      <td>{result.TotalQuestions*2}</td>
                    </tr>
                    <tr>
                      <td>Obtain Marks:</td>
                      <td>{result.CorrectQuestions*2}</td>
                    </tr>
                    <tr>
                      <td>Percentage:</td>
                      <td>{result.Percentage}</td>
                    </tr>
                  </tbody>
                </Table>
                <div className="text-center mt-4">
                  <Button
                    variant="danger"
                    className="mb-4"
                    onClick={handleLogout}
                    style={{ width: '150px' }}
                  >
                    Logout
                  </Button>
                </div>
                <div className="text-center mt-2">
                  <img className="rajlogo" style={{ width: 50, height: 50 }} src={RajLogo} alt="Raj Logo" />
                </div>
              </div>
            </div>
          </div>
          <div className="col-12 col-md-6 col-lg-0 d-flex justify-content-center align-items-center d-none d-md-flex">
            <img className="exam-logo" style={{ width: '600px', height: 'auto', marginLeft:'350px' }} src={LearnigImg} alt="Exam Login" />
          </div>
        </div>
      </Container>
    </div>
  );
};

export default NewResult;
