// src/App.js
import React from 'react';
import { Routes, Route, useLocation } from 'react-router-dom';

import Login from './components/Login';
import Header from './components/Header';
import HeaderExam from './components/HeaderExam';
import Footer from './components/Footer';
import StartExam from './components/StartExam';
import Questions from './components/Questions';
import Result from './components/Result';
import PageNotFound from './components/404-page';
import Layout from './dashboard/Layout';
import AdminDashboard from './dashboard/AdminDashboard';
import SDashboard from './components/SDashboard';
import NewResult from './components/NewResult';
import { Home } from './Home';
import HomeNavBar from './components/HomeNavBar';
import CentreLogin from './components/CentreLogin';
import AdminLogin from './components/AdminLogin';
import CentreDashboard from './dashboard/CentreDashboard';


function App() {
  const location = useLocation();

  const showHeaderExam = location.pathname === '/question';
  const showHomeNavBar = location.pathname === '/' ;
  const showHeader = location.pathname !== '/login' && !showHeaderExam && !location.pathname.startsWith('/dashboard') && location.pathname !== '/finalresult' && location.pathname !== '/' && location.pathname !== '/centrelogin' && location.pathname !== '/adminlogin';
  const showFooter = location.pathname !== '/login' && !location.pathname.startsWith('/dashboard') && location.pathname !== '/finalresult' && location.pathname !== '/centrelogin' && location.pathname !== '/adminlogin';

  return (
    <div className="App">
      {showHeaderExam && <Header />}
      {showHeader && <HeaderExam /> }
      {showHomeNavBar && <HomeNavBar/>}
      
      <Routes>
        <Route path="/" element={<Home/>} />
        <Route path='/login' element={<Login />} />
        <Route path='/centrelogin' element={<CentreLogin />} />
        <Route path='/centredashboard' element={<CentreDashboard />} />
        <Route path='/adminlogin' element={<AdminLogin />} />
        <Route path="/examination" element={<StartExam />} />
        <Route path="/question" element={<Questions />} />
        <Route path="/result" element={<Result />} />
        <Route path="/finalresult" element={<NewResult />} />
        <Route path="/dashboard/*" element={<Layout/>}>
          <Route path="demo" element={<SDashboard />} />
          <Route path="" element={<AdminDashboard />} />
        </Route>
        <Route path="*" element={<PageNotFound />} />
      </Routes><br/>
      
      {showFooter && <Footer />}
    </div>
  );
}

export default App;
