import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import '../assets/css/timer.css';

const ExamTimer = () => {
  const navigate = useNavigate();

  const getCurrentTimeInSeconds = () => Math.floor(Date.now() / 1000);

  const calculateRemainingTime = useCallback(
    (endTime) => Math.max(0, endTime - getCurrentTimeInSeconds()),
    []
  );

  const initializeRemainingTime = useCallback(() => {
    const savedEndTime = localStorage.getItem('examEndTime');
    if (savedEndTime) {
      const endTime = parseInt(savedEndTime, 10);
      return calculateRemainingTime(endTime);
    }
    return 3600; // Default time if not found
  }, [calculateRemainingTime]);

  const [remainingTime, setRemainingTime] = useState(() => initializeRemainingTime());

  useEffect(() => {
    const savedEndTime = localStorage.getItem('examEndTime');
    const endTime = savedEndTime ? parseInt(savedEndTime, 10) : getCurrentTimeInSeconds() + 3600;
    localStorage.setItem('examEndTime', endTime);

    const timerId = setInterval(() => {
      setRemainingTime(prevTime => {
        if (prevTime <= 1) {
          clearInterval(timerId);
          localStorage.removeItem('examEndTime'); // Clear the end time from localStorage
          navigate('/result');
          return 0;
        }
        return calculateRemainingTime(endTime);
      });
    }, 1000);

    return () => clearInterval(timerId);
  }, [navigate, calculateRemainingTime]);

  const formatTime = (seconds) => {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes} : ${remainingSeconds < 10 ? '0' : ''}${remainingSeconds}`;
  };

  return (
    <div className='timer'>
      <p style={{ justifyContent: 'center' }}>{formatTime(remainingTime)}</p>
    </div>
  );
};

export default ExamTimer;
