import React, { useContext } from 'react';
import { MDBContainer, MDBNavbar, MDBNavbarBrand } from 'mdb-react-ui-kit';
import { Col, Row } from 'react-bootstrap';
import '../assets/css/header.css';
import MBTBLogo from '../assets/img/MBTB-Logo.jpg';
import ExamTimer from './ExamTimer';
import { UserContext } from '../contexts/UserContext';

export default function App() {
  const { user } = useContext(UserContext);

  return (
    <>
      <Row>
        <Col>
          <MDBNavbar light className='Navbar'>
            <MDBContainer fluid className="d-flex flex-column flex-md-row align-items-center">
              <MDBNavbarBrand href='/exam' className="d-flex align-items-center mb-2 mb-md-0">
                <img
                  src={MBTBLogo}
                  height='40'
                  alt='MBTB Logo'
                  loading='lazy'
                  className="me-2"
                />
                <span className='brand-text'>MBTB ONLINE EXAMINATION</span>
              </MDBNavbarBrand>

              <div className="ms-auto d-flex flex-column align-items-md-end mb-2 mb-md-0">
                <span className='name'>
                  Name: {`${user?.firstname || ''} ${user?.middlename || ''} ${user?.lastname || ''}`.trim()}
                </span>
                <span className='course mt-1'>
                  Course: {`${user?.courseid || ''}`}
                </span>
              </div>

              <div className="ms-md-4 mt-2 mt-md-0">
                <ExamTimer />
              </div>
            </MDBContainer>
          </MDBNavbar>
        </Col>
      </Row>
    </>
  );
}
