import React, { useContext, useEffect, useState, useMemo } from 'react';
import DataTable from 'react-data-table-component';
import axios from 'axios';
import { UserContext } from '../contexts/UserContext';

const CentreDashboard = () => {
    const [records, setRecords] = useState([]);
    const [filteredRecords, setFilteredRecords] = useState([]);
    const { user } = useContext(UserContext);

    useEffect(() => {
        const fetchDataCentrelist = async () => {
            if (!user?.username || !user?.password) {
                console.error('Username or password is missing.');
                return;
            }

            try {
                const response = await axios.post('https://sdvtii.in/api-admin/api/admin-login', {
                    username: user.username,
                    password: user.password,
                });

                console.log('Login successful:', response.data);

                const { students = [],  varCentreshrtName } = response.data;

                // Map over students to include center representative info and full name
                const formattedData = students.map(student => ({
                    dttSubmitDate: student.attendance?.dttSubmitDate,
                    marks: student.attendance?.marks,
                    chrCourseId: student.course?.chrCourseId,
                    chrStudentCode: student.chrStudentCode.trim(), // Trim whitespace
                    fullName: `${student.varFirstName} ${student.varMiddleName || ''} ${student.varLastName}`.trim(),                   
                    varCentreshrtName,
                }));

                console.log("Fetched student data:", formattedData);
                setRecords(formattedData);
                setFilteredRecords(formattedData);
            } catch (error) {
                console.error('Error fetching centre data:', error.response ? error.response.data : error.message);
            }
        };

        fetchDataCentrelist();
    }, [user]);

    const handleFilter = (event) => {
        const searchTerm = event.target.value.toLowerCase();
        const newFilteredData = records.filter(row =>
            row.fullName.toLowerCase().includes(searchTerm) ||
            row.chrStudentCode.toLowerCase().includes(searchTerm)
        );
        setFilteredRecords(newFilteredData);
    };

    
    const columns = useMemo(() => [
        {
            name: 'Student Code',
            selector: row => row.chrStudentCode,
            sortable: true,
        },
        {
            name: 'Full Name',
            selector: row => row.fullName,
            sortable: true,
        },
        
        {
            name: 'Centre',
            selector: row => row.varCentreshrtName,
            sortable: true,
        },
        {
            name: 'Course',
            selector: row => row.chrCourseId,
            sortable: true,
        },
        {
            name: 'Submit',
            selector: row => row.dttSubmitDate,
            sortable: true,
        },
        {
            name: 'Marks',
            selector: row => row.marks,
            sortable: true,
        },
    ], []);

    return (
        <div className='container mt-4'>
              <h4 className='text-center'style={{textShadow:'2px 2px #00A9FF'}}>Centre Dashboard</h4><hr />
            <div className='text-end mb-2'>              
                <input 
                    type="text" 
                    placeholder='Search...' 
                    onChange={handleFilter} 
                />
            </div>
            <DataTable
                columns={columns}
                data={filteredRecords}
                fixedHeader
                pagination
            />
        </div>
    );
};

export default CentreDashboard;
