import React, { useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Container, Form, Button, Modal } from 'react-bootstrap';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';
import RajLogo from '../assets/img/Raj-Logo.png';
import Logo1 from '../assets/img/MBTB-Marathi.png';
import { UserContext } from '../contexts/UserContext';

const CentreLogin = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [showModal, setShowModal] = useState(false);
  const navigate = useNavigate();
  const { saveUser } = useContext(UserContext);

  const handleLogin = async (e) => {
    e.preventDefault();

    if (!username || !password) {
      setError('Please enter both username and password.');
      setShowModal(true);
      return;
    }

    try {
      const response = await axios.post('https://sdvtii.in/api-admin/api/admin-login', { username, password });
      
      console.log('Login successful:', response.data);
      saveUser(response.data);
     
      if (response.data.chrCentrecode !== null) {
        navigate('/centredashboard');
      } else {
        setError('Centre code is invalid.');
        setShowModal(true);
      }

    } catch (error) {
      console.error('Login failed:', error.response ? error.response.data : error.message);
      setError('Invalid username or password.');
      setShowModal(true);
    }
  };

  const handleClose = () => {
    setShowModal(false);
    setError(''); // Clear error when modal is closed
  };

  return (
    <div className='login'>
      <Container className="d-flex justify-content-center align-items-center min-vh-100 p-3">
        <div className="card shadow-sm w-100" style={{ maxWidth: '400px' }}>
          <div className="card-body" style={{ padding: '30px' }}>
            <div className="text-center mb-4">
              <a href="/exam" title='Go to the home page'><img className="imgcentre" src={Logo1} alt="Online Exam" /></a>
            </div>
            <p className="text-center mb-4">Sign in to start your Centre</p>
            <Form onSubmit={handleLogin}>
              <Form.Group controlId="username">
                <Form.Label>Username:</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter your username"
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                  required
                />
              </Form.Group>
              <Form.Group controlId="password" className="mt-3">
                <Form.Label>Password:</Form.Label>
                <Form.Control
                  type="password"
                  placeholder="Enter your password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
              </Form.Group>
              <Button variant="primary" type="submit" className="w-100 mt-4">
                Login
              </Button>
              <div className="text-center mt-3">
                <img className="rajlogo" style={{ width: 50, height: 50 }} src={RajLogo} alt="Raj Logo" />
              </div>
            </Form>
          </div>
        </div>
      </Container>
      <Modal show={showModal} onHide={handleClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Error</Modal.Title>
        </Modal.Header>
        <Modal.Body>{error}</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default CentreLogin;
