import React, { useState, useEffect, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import ApiService from '../services/ApiService';
import { Container, Form, FormGroup, Row, Col, Button, Navbar, Nav, Offcanvas, Modal } from 'react-bootstrap';
import { Input, Label } from 'reactstrap';
import { useMediaQuery } from 'react-responsive';
import '../assets/css/question.css';
import NumberOfQuestion from './NumberOfQuestion';
import { UserContext } from '../contexts/UserContext';

const Questions = () => {
  const [attemptedQuestions, setAttemptedQuestions] = useState([]);
  const [questions, setQuestions] = useState([]);
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [loadingQuestions, setLoadingQuestions] = useState(false);
  const [selectedOptions, setSelectedOptions] = useState({});
  const [totalQuestion, setTotalQuestion] = useState(0);
  const [showOffcanvas, setShowOffcanvas] = useState(false);
  const { user } = useContext(UserContext);
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const isMobile = useMediaQuery({ query: '(max-width: 1200px)' });
  const navigate = useNavigate();

  useEffect(() => {
    const fetchInitialQuestion = async () => {
      try {
        setLoadingQuestions(true);
        const response = await ApiService.getQuestions(1, user?.username, user?.courseid);
        setQuestions([response.data.Questions]);
        console.log(response.data)
        setTotalQuestion(response.data.totalquest);
        setLoadingQuestions(false);

        // Load from localStorage
        const savedState = JSON.parse(localStorage.getItem('quizState'));
        if (savedState) {
          setAttemptedQuestions(savedState.attemptedQuestions || []);
          setQuestions(savedState.questions || []);
          setCurrentQuestionIndex(savedState.currentQuestionIndex || 0);
          setSelectedOptions(savedState.selectedOptions || {});
        }
      } catch (error) {
        console.error('Error fetching initial question:', error);
        setLoadingQuestions(false);
      }
    };
    fetchInitialQuestion();
  }, [user?.username, user?.courseid]);

  const saveStateToLocalStorage = () => {
    localStorage.setItem('quizState', JSON.stringify({
      attemptedQuestions,
      questions,
      currentQuestionIndex,
      selectedOptions,
    }));
  };

  const fetchQuestion = async (questionNumber) => {
    try {
      setLoadingQuestions(true);
      const response = await ApiService.getQuestions(questionNumber, user?.username, user?.courseid);
      if (!response.data.Questions || response.data.Questions.length === 0) {
        // Handle null or empty question data
        setLoadingQuestions(false);
        if (window.confirm('Internal Server Error. Click OK to refresh the page.')) {
          window.location.reload(); // Refresh the page
        }
        return;
      }
      setQuestions(prevQuestions => {
        const newQuestions = [...prevQuestions];
        newQuestions[questionNumber - 1] = response.data.Questions;
        return newQuestions;
      });
      setLoadingQuestions(false);
      saveStateToLocalStorage();
    } catch (error) {
      console.error(`Error fetching question ${questionNumber}:`, error);
      setLoadingQuestions(false);
      if (window.confirm('Internal Server Error. Click OK to refresh the page.')) {
        window.location.reload(); // Refresh the page
      }
    }
  };

  const handleOptionChange = (optionId) => {
    setSelectedOptions(prevSelectedOptions => ({
      ...prevSelectedOptions,
      [currentQuestionIndex]: optionId
    }));
    saveStateToLocalStorage();
  };

  const handleQuestionClick = (number) => {
    setCurrentQuestionIndex(number - 1);
    if (!questions[number - 1]) {
      fetchQuestion(number);
    }
    setShowOffcanvas(false);
  };

  const handleNext = async () => {
    const currentQuestId = questions[currentQuestionIndex]?.questid;
    const selectedOption = selectedOptions[currentQuestionIndex];
    if (selectedOption !== null && selectedOption !== undefined) {
      await ApiService.saveResponse(currentQuestId, selectedOption, user?.username);
      if (!attemptedQuestions.includes(currentQuestionIndex + 1)) {
        setAttemptedQuestions([...attemptedQuestions, currentQuestionIndex + 1]);
      }
    }
    saveStateToLocalStorage();

    const nextQuestionIndex = currentQuestionIndex + 1;
    if (nextQuestionIndex < totalQuestion) {
      setCurrentQuestionIndex(nextQuestionIndex);
      if (!questions[nextQuestionIndex]) {
        await fetchQuestion(nextQuestionIndex + 1);
      }
    }
  };

  const handlePrevious = async () => {
    const currentQuestId = questions[currentQuestionIndex]?.questid;
    const selectedOption = selectedOptions[currentQuestionIndex];
    if (selectedOption !== null && selectedOption !== undefined) {
      await ApiService.saveResponse(currentQuestId, selectedOption, user?.username);
    }
    saveStateToLocalStorage();

    const previousQuestionIndex = currentQuestionIndex - 1;
    if (previousQuestionIndex >= 0) {
      setCurrentQuestionIndex(previousQuestionIndex);
      if (!questions[previousQuestionIndex]) {
        await fetchQuestion(previousQuestionIndex + 1);
      }
    }
  };

  const handleSubmit = async () => {
    try {
      const currentQuestId = questions[currentQuestionIndex]?.questid;
      const selectedOption = selectedOptions[currentQuestionIndex];
      if (currentQuestId && selectedOption !== null && selectedOption !== undefined) {
        await ApiService.saveResponse(currentQuestId, selectedOption, user?.username);
        if (!attemptedQuestions.includes(currentQuestionIndex + 1)) {
          setAttemptedQuestions([...attemptedQuestions, currentQuestionIndex + 1]);
        }
      }
      await ApiService.getResult(user?.username, user?.courseid);
      navigate('/finalresult');
      localStorage.removeItem('quizState'); // Clear state from localStorage on submit
    } catch (error) {
      console.error('Error submitting results:', error);
    }
  };

  const handleClearSelection = () => {
    setSelectedOptions(prevSelectedOptions => ({
      ...prevSelectedOptions,
      [currentQuestionIndex]: null
    }));
    setAttemptedQuestions(prevAttemptedQuestions =>
      prevAttemptedQuestions.filter(qIndex => qIndex !== currentQuestionIndex + 1)
    );
    saveStateToLocalStorage();
  };

  const renderQuestionNumber = (number) => {
    const questionStatus = getQuestionStatus(number);
    let colorClass = '';
    switch (questionStatus) {
      case 'attempted':
        colorClass = 'custom-bg-color';
        break;
      case 'skipped':
        colorClass = 'custom-bg-buttom';
        break;
      case 'notAttempted':
        colorClass = 'bg-danger text-black';
        break;
      default:
        colorClass = '';
    }
    return (
      <Nav.Link
        className={`question-number ${colorClass}`}
        onClick={() => handleQuestionClick(number)}
      >
        {number}
      </Nav.Link>
    );
  };

  const getQuestionStatus = (number) => {
    if (attemptedQuestions.includes(number)) {
      return 'attempted';
    } else if (currentQuestionIndex + 1 === number) {
      return 'notAttempted';
    } else {
      return 'skipped';
    }
  };

  const currentQuestion = questions[currentQuestionIndex] || {};

  return (
    <Container fluid className="min-vh-100 d-flex flex-column containerbody">
      <Nav className="me-auto mt-2">
        {isMobile && (
          <Button
            variant="primary"
            onClick={() => setShowOffcanvas(true)}
            className="mobile-offcanvas-button"
          >
            Show Questions
          </Button>
        )}
      </Nav>

      <Navbar.Text>
        <Row>
          <Col>
            <h5>
              <span>{currentQuestionIndex + 1}/{totalQuestion}</span>
            </h5>
          </Col>
        </Row>
      </Navbar.Text>

      <Row className="justify-content-center">
        <Col xs={12} md={8}>
          <p className="mb-4 questions no-select">{currentQuestion?.question}</p>
          {loadingQuestions ? (
            <p>Loading...</p>
          ) : (
            <Form>
              {currentQuestion?.options && currentQuestion.options
                .filter(option => option.varOptionValue && option.varOptionValue.trim() && option.varOptionValue !== '.')
                .map((option) => (
                  <FormGroup key={option.chroptionuniqueid}>
                    <Label
                      className={`d-flex align-items-center mb-3 ${selectedOptions[currentQuestionIndex] === option.chroptionuniqueid ? 'selected-option' : ''}`}
                    >
                      <Input
                        type="radio"
                        name={`question${currentQuestionIndex}`}
                        value={option.chroptionuniqueid}
                        className="me-2"
                        id="specifyColor"
                        checked={selectedOptions[currentQuestionIndex] === option.chroptionuniqueid}
                        onChange={() => handleOptionChange(option.chroptionuniqueid)}
                      />
                      <span className="options">{option.varOptionValue}</span>
                    </Label>
                  </FormGroup>
                ))}
            </Form>
          )}

          <Row className="mt-4 justify-content-center">
            <Col xs={3} sm="auto" className="mb-2 mb-md-0">
              <Button
                variant="secondary"
                className="w-100"
                onClick={handlePrevious}
                disabled={currentQuestionIndex === 0}
              >
                Previous
              </Button>
            </Col>
            <Col xs={3} sm="auto" className="mb-2 mb-md-0">
              <Button
                type="reset"
                variant="secondary"
                className="w-100"
                onClick={handleClearSelection}
              >
                Clear
              </Button>
            </Col>
            <Col xs={3} sm="auto">
              {currentQuestionIndex === totalQuestion - 1 ? (
                <Button variant="success" className="w-100" onClick={handleShow}>Submit</Button>
              ) : (
                <Button variant="secondary" className="w-100" onClick={handleNext}>Next</Button>
              )}
            </Col>
          </Row>
        </Col>
        <NumberOfQuestion
          attemptedQuestions={attemptedQuestions}
          totalQuestions={totalQuestion}
          currentQuestionIndex={currentQuestionIndex}
          handleQuestionClick={handleQuestionClick}
        />
      </Row>

      <Modal show={show}>
        <Modal.Header closeButton>
          <Modal.Title>Online Examination</Modal.Title>
        </Modal.Header>
        <Modal.Body>Do you want to submit the exam?</Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={handleClose}>
            No
          </Button>
          <Button variant="success" onClick={handleSubmit}>
            Yes
          </Button>
        </Modal.Footer>
      </Modal>

      <Offcanvas show={showOffcanvas} onHide={() => setShowOffcanvas(false)} placement="start">
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>Questions</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <Nav className="flex-column">
            <Row className="justify-content-center">
              {[...Array(totalQuestion).keys()].map((number) => (
                <Col xs="auto" key={number + 1}>
                  {renderQuestionNumber(number + 1)}
                </Col>
              ))}
            </Row>
          </Nav>
        </Offcanvas.Body>
      </Offcanvas>
    </Container>
  );
};

export default Questions;
