import React from 'react';
import { MDBContainer, MDBNavbar, MDBNavbarBrand } from 'mdb-react-ui-kit';
import { Col, NavDropdown, Row } from 'react-bootstrap';
import MBTBLogo from '../assets/img/MBTB-Logo.jpg';

function HomeNavBar() {
  return (
    <Row>
      <Col xs={12}>
        <MDBNavbar light className='Navbar'>
          <MDBContainer fluid>
            <MDBNavbarBrand href='/exam' style={{ font: 'initial', marginLeft: '30px', marginRight: 'auto' }}>
              <img
                src={MBTBLogo}
                height='40'
                alt='MBTB Logo'
                loading='lazy'
              />
              <span className='brand-text'>MBTB ONLINE EXAMINATION</span>
            </MDBNavbarBrand>
            <NavDropdown title="LOGIN HERE" id="collapsible-nav-dropdown" className="ml-auto">
              <NavDropdown.Item href="/exam/login" style={{ fontSize: '13px' }}>STUDENT LOGIN</NavDropdown.Item>
              <NavDropdown.Item href="/exam/centrelogin" style={{ fontSize: '13px' }}>CENTRE LOGIN</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="/exam/adminlogin" style={{ fontSize: '13px' }}>
                ADMINISTRATION LOGIN
              </NavDropdown.Item>
            </NavDropdown>
          </MDBContainer>
        </MDBNavbar>
      </Col>
    </Row>
  );
}

export default HomeNavBar;
