import React, { useContext, useEffect, useState } from 'react';
import { Card, Container, Row, Col, Table, Button } from 'react-bootstrap';
import ApiService from '../services/ApiService';
import { UserContext } from '../contexts/UserContext'; // Import UserContext
import '../assets/css/result.css';
import { useNavigate } from 'react-router-dom';

const Result = () => {
  const [result, setResult] = useState(null);
  const { user } = useContext(UserContext); // Use user from UserContext
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  const handleLogout = () => {
    localStorage.clear();   
    navigate('/'); 
  };

  useEffect(() => {
    const fetchResult = async () => { 
      try {
        setLoading(true);
        if (user?.username && user?.courseid) {
          const response = await ApiService.getResult(user.username, user.courseid);
          console.log(response);
          setResult(response.data);
        }
        setLoading(false);
      } catch (error) {
        console.error('Error fetching result:', error);
        setLoading(false);
      }
    };

    fetchResult();
  }, [user]);

  if (loading) {
    return <p>Loading...</p>;
  }

  if (!result) {
    return <p>No result data available.</p>;
  }

  return (
    <div className='col'>   
      <Container fluid className='resultBackground'>
        <Row className="justify-content-center">
          <Button 
            variant="danger" 
            className="mb-4 text-center" 
            onClick={handleLogout}
            style={{ width: '150px' }}
          >
            Logout
          </Button>
        </Row>
        <Row className="justify-content-center">
          <Col xs={12} sm={10} md={8} lg={6}>
            <Card className='card'><br/>
              <h6 className='text-center'>Name:  {`${user?.firstname || ''} ${user?.middlename || ''} ${user?.lastname || ''}`.trim()}</h6>
              <h6 className='text-center'>Course:  {`${user?.courseid || ''}`}</h6>
              <Card.Body className='cardBody'>
                <Table bordered>
                  <tbody>
                    <tr>
                      <td>Total Questions:</td>
                      <td>{result.TotalQuestions}</td>
                    </tr>
                    <tr>
                      <td>Skipped Questions:</td>
                      <td>{result.Skipped}</td>
                    </tr>
                    
                    <tr>
                      <td>Attempted Questions:</td>
                      <td>{result.Attempted}</td>
                    </tr>
                    <tr>
                      <td>Correct Questions:</td>
                      <td>{result.CorrectQuestions}</td>
                    </tr>
                    <tr>
                      <td>Total Marks:</td>
                      <td>{result.Skipped}</td>
                    </tr>
                    <tr>
                      <td>Percentage:</td>
                      <td>{result.Percentage}</td>
                    </tr>
                  </tbody>
                </Table>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Result;
