import React, { useContext, useState } from 'react';
import { Container, Button, Modal } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import '../../src/assets/css/startexam.css';
import { UserContext } from '../contexts/UserContext'; // Import UserContext

const StartExam = () => {
  const [modal, setModal] = useState(false);
  const [modal1, setModal1] = useState(false);
  const { user } = useContext(UserContext); // Use user and setUser from UserContext
  const navigate = useNavigate();

  const toggle = () => setModal(!modal);
  const toggle1 = () => setModal1(!modal1);
 
  const handleStartExam = () => {
    navigate('/question'); // No need to call toggle1 as navigation will hide the modal
  };

  return (
    <div className='startExams'>
      <div className="container-fluid page-header py-4 py-md-6 my-md-4 mt-0 wow fadeIn" data-wow-delay="0.1s">
        <Container className="text-center">
          <h1 className="display-3 text-dark mb-4 animated slideInDown">
            Welcome to Your Exam
            <div>{`${user?.firstname || ''} ${user?.middlename || ''} ${user?.lastname || ''}`.trim()}</div>
          </h1>
          <Button 
            onClick={toggle} 
            className="btn-solid-reg popup-with-move-anim btn btn-primary py-3 py-sm-3 px-4 px-sm-5 me-2 me-sm-3" 
            style={{ boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.1)' }}
            aria-label="Show Exam Instructions"
          >
            Show Instructions
          </Button>
          <Button 
            onClick={toggle1} 
            className="btn btn-light py-3 py-sm-3 px-4 px-sm-5" 
            id="startExamButton" 
            style={{ boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.1)' }}
            aria-label="Start Exam"
          >
            Start Exam
          </Button>
        </Container>
      </div>

      {/* Modal for Show Instructions */}
      <Modal show={modal} onHide={toggle} dialogClassName="custom-modal">
        <Modal.Header closeButton>
          <Modal.Title>Online Exam Instructions</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p><b>Login:</b> Log in to the examination portal using your provided credentials at least 15 minutes before the start time.</p>
          <p><b>Technical Requirements:</b> Ensure your device meets the technical requirements specified. Use a stable internet connection.</p>
          <p><b>Duration:</b> You have 45 minutes to complete the exam. A countdown timer will be displayed.</p>
          <p><b>Browser Settings:</b> Use a compatible browser (Chrome, Firefox, Safari) with pop-up blockers disabled.</p>
          <p><b>Exam Interface:</b> Familiarize yourself with the exam interface. Navigate through questions using the provided navigation tools.</p>
          <p><b>Answer Format:</b> Answer all questions directly on the platform. Save answers periodically to prevent loss of data.</p>
          <p><b>Honesty Policy:</b> Do not seek or provide unauthorized assistance during the exam. Plagiarism detection software will be used.</p>
          <p><b>Problems:</b> Contact the center manager immediately if you encounter technical issues that prevent you from continuing the exam.</p>
          <p><b>Submission:</b> Submit your answers before the time expires. Ensure all responses are saved and confirmed.</p>
          <p><b>Logout:</b> Don't forget to log out from the exam portal after submitting your answers.</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="success" onClick={toggle1}>
            Continue
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Modal for Start Exam */}
      <Modal show={modal1} onHide={toggle1}>
        <Modal.Header closeButton>
          <Modal.Title>Start Exam Confirmation</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
            Are you sure you want to start the exam now? Once started, the timer will begin and cannot be paused.
          </p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="success" onClick={handleStartExam}> 
            Start Exam
          </Button>
          <Button variant="danger" onClick={toggle1}>
            Cancel
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default StartExam;
