// src/dashboard/Client.jsx
import React from 'react';
import 'bootstrap-icons/font/bootstrap-icons.css';
import '../assets/css/client.css';
import { Link } from 'react-router-dom';

const SideBar = () => {
  return (
    <div className='sidebar d-flex flex-column justify-content-between bg-white text-dark p-4 vh-100'>
      <div>
        <Link to='/dashboard' className='d-flex align-items-center text-dark text-decoration-none'>
          <i className='bi bi-bootstrap me-2 icons'></i>
          <span className='fs-6'>MBTB Dashboard</span>
        </Link>

        <hr className='text-secondary' />
        <ul className='nav nav-pills flex-column p-0 m-0'>
          <li className='nav-item'>
            <Link to='/dashboard/demo' className='nav-link text-dark'>
              <i className='bi bi-speedometer me-2 icons'></i>
              <span className='content'>S-Dashboard</span>
            </Link>
          </li>
          <li className='nav-item'>
            <Link to='/order' className='nav-link text-dark'>
              <i className='bi bi-cart me-2 icons'></i>
              <span className='content'>Order</span>
            </Link>
          </li>
          <li className='nav-item'>
            <Link to='/customers' className='nav-link text-dark'>
              <i className='bi bi-person me-2 icons'></i>
              <span className='content'>Customers</span>
            </Link>
          </li>
          <li className='nav-item'>
            <Link to='/reports' className='nav-link text-dark'>
              <i className='bi bi-file-earmark-bar-graph me-2 icons'></i>
              <span className='content'>Reports</span>
            </Link>
          </li>
        </ul>
      </div>
      <div>
        <hr className='text-secondary' />
        <i className='bi bi-person'></i>
        <span className='text-dark'>MBTB</span>
      </div>
    </div>
  );
};

export default SideBar;
