import React, { useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Container, Form, Button, Modal } from 'react-bootstrap';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../assets/css/login.css';
import RajLogo from '../assets/img/Raj-Logo.png';
import Logo1 from '../assets/img/MBTB-Marathi.png';
import LearnigImg from '../assets/img/Exam-Login.png';
import { UserContext } from '../contexts/UserContext'; // Import UserContext

const Login = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [showModal, setShowModal] = useState(false);
  const navigate = useNavigate();
  const { saveUser } = useContext(UserContext); // Use saveUser from UserContext

  const handleLogin = async (e) => {
        e.preventDefault(); 

    if (!username || !password) {
      setError('Please enter both username and password.');
      setShowModal(true);
      return;
    } try {     
      const response = await axios.post('https://sdvtii.in/api-login/auth/signin', { username, password });

      console.log('Login successful:', response.data);
      saveUser(response.data); 

      if (response.data.authorisation === '1') {
        navigate('/finalresult');
      } else {
        navigate('/examination');
      }
    } catch (error) {
      console.error('Login failed:', error.response ? error.response.data : error.message);
      setError('Invalid username or password.');
      setShowModal(true);
    }
  };

  const handleClose = () => setShowModal(false);

  return (
    <div className='login'>
      <Container className="d-flex align-items-center min-vh-100 p-3">
        <div className="row w-100">
          <div className="col-12 col-md-6 col-lg-5">
            <div className="card shadow-sm" style={{ marginLeft: '10px' }}>
              <div className="card-body" style={{ padding: '30px' }}>
                <div className="text-center">
                <a href="/exam" title='Go to the home page'>  <img className="imgcentre mb-4" src={Logo1} alt="Online Exam" /></a>
                </div>
                <p className="text-center mb-4">Sign in to start your session</p>
                <Form onSubmit={handleLogin}>
                  <Form.Group controlId="username">
                    <Form.Label>Username:</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter your username"
                      value={username}
                      onChange={(e) => setUsername(e.target.value)}
                      required
                    />
                  </Form.Group>
                  <Form.Group controlId="password" className="mt-3">
                    <Form.Label>Password:</Form.Label>
                    <Form.Control
                      type="password"
                      placeholder="Enter your password"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      required
                    />
                  </Form.Group>
                  <Button variant="primary" type="submit" className="w-100 mt-4">
                    Login
                  </Button>
                  <Button 
                    type="button" 
                    className="d-block text-center mt-2 btn btn-link" 
                    style={{ fontSize: '13px' }}
                    onClick={() => alert('Contact your nearest branch for help.')}
                  >
                    If you are unable to login, contact your nearest branch
                  </Button>
                  <div className="text-center mt-3">
                    <img className="rajlogo" style={{ width: 50, height: 50 }} src={RajLogo} alt="Raj Logo" />
                  </div>
                </Form>
              </div>
            </div>
          </div>
          <div className="col-12 col-md-6 col-lg-4 d-flex justify-content-center align-items-center d-none d-md-flex">
            <img className="exam-logo" style={{ width: '600px', height: 'auto' }} src={LearnigImg} alt="Learning" />
          </div>
        </div>
      </Container>
      <Modal show={showModal} onHide={handleClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Error</Modal.Title>
        </Modal.Header>
        <Modal.Body>{error}</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default Login;
