import React from 'react';
import ErrorPage from '../assets/img/404Error.webp';

const PageNotFound = () => {
    return (
        <div id="wrapper" style={styles.wrapper}>
            <img src={ErrorPage} alt="404 Error - Page Not Found" style={styles.image} />
            <div id="info" style={styles.info}>
                <h3>This page could not be found</h3>
            </div>
        </div>
    );
};

const styles = {
    wrapper: {
        textAlign: 'center',
        padding: '20px',
    },
    image: {
        width: '100%',
        maxWidth: '600px',
        height: 'auto',
    },
    info: {
        marginTop: '20px',
    },
};

export default PageNotFound;
