import axios from 'axios';


const ONLINE_EXAM_API_BASE_URL = 'https://sdvtii.in/api-question/api';// http://amar:8082/api

const saveResponse_base_Url = 'https://sdvtii.in/api-question/api/saveResponse';//http://amar:8082/api/saveResponse

const showResult = 'https://sdvtii.in/api-question/api'; //http://amar:8082/api

const Centre_List = 'https://sdvtii.in/api-admin/api';//http://amar:9001/api

const Student_List = "https://sdvtii.in/api-admin/api";//http://amar:9001/api

const Reset_Exam_Of_Student = 'https://sdvtii.in/api-admin/api/';


//Login User
const ApiService = {
 

  //Get Question through questId 
  getQuestions: (id,username, coursecode) => {
   
    return axios.post(`${ONLINE_EXAM_API_BASE_URL}/questions/${id}`,{username, coursecode} );
  },


  //save quetions of student Recored
  saveResponse : (questId,optionId,username) => {    
  
    return axios.post(`${saveResponse_base_Url}`, {questId, optionId, username});
  },


// get Exam Result
  getResult: (username,coursecode) =>{

     return axios.post(`${showResult}/result`,{username,coursecode});

  },

//get CentreList details
getCentreList: async () =>{

  return await axios.get(`${Centre_List}/centrelist`);

},

 // Update this method to send 'centreCode' in the request body
 getListOfStudent: async (chrCentrecode) => {

  return await axios.post(`${Student_List}/studentlist`, { chrCentrecode });

},


// Reset response Attendance of students
resetExamOfStudent: async (username) => {

  return await axios.delete(`${Reset_Exam_Of_Student}/deleteResponse/${username}`);
  
},


};

export default ApiService;
